
<template>
<v-container>
      <h1>Пользователи</h1>
      <div class="modal"
      v-if="modal == true">
        <v-card class="pa-2 modal-card">
          Редактирование пользователя: 
          <v-text-field
            v-for="(t, index) in modalInput"
            :key="t.id"
            v-model="t.index"
            :rules="nameRules"
            :counter="10"
            :label= index
            required
          ></v-text-field>
          <div class="btn_block">
          <v-btn @click="modal = false">
            Button
          </v-btn>
          <v-btn @click="modal = false">
            Button
          </v-btn>
          <v-btn class="ban-btn" @click="modal = false">
            Забанить
          </v-btn>
          </div>

        </v-card>
      </div>
      <v-row>
        <v-col
          cols="4"
          md="4"
        >
          <v-text-field
            v-model="filter"
            label="Поиск по Имени/Фамилии/Никнейму/Почте"
            prepend-icon="mdi-magnify"
            required
          >
          </v-text-field>

        </v-col>
        <v-checkbox
          v-model="checkboxDelite"
          :label="`Только Удален`"
        ></v-checkbox>
        <v-checkbox
          v-model="checkboxBlocket"
          :label="`Только Заблокирован`"
        ></v-checkbox>
      </v-row>
      
  <v-table density="compact">
    <thead>
      <tr>
        <th
        v-for="t in TableHead"
        :key="t"
        class="text-left">
          {{t}}
        </th> 
      </tr>
    </thead>
    <tbody>
      <tr
        v-for="u in filteredList().slice((this.page - 1) * this.Pagination, this.page * this.Pagination)"
        :key="u"
        @click="modalStart(u)"
        class="table"
      >
        <td>{{ u.neme }}</td>
        <td>{{ u.surname }}</td>
        <td>{{ u.gender }}</td>
        <td>{{ u.nickname }}</td>
        <td>{{ u.email }}</td>
        <td>{{ u.old }}</td>
        <td>{{ u.interests }}</td>
        <td>{{ u.activity }}</td>
        <td>{{ u.dell }}</td>
        <td>{{ u.blocket }}</td>
      </tr>
    </tbody>
  </v-table>
  <v-row>
  <v-col
  >
    <v-pagination 
    :total-visible="Pagination"
    v-model="page"
    :length="Math.ceil(filteredList().length / Pagination)"></v-pagination>
  </v-col>
  </v-row>

  </v-container>
</template>

<script>
export default {
  data(){
    return{
      modal: false,
      modalInput: [],
      checkboxDelite: false,
      checkboxBlocket: false,
      page: 1,
      Pagination: 8,
      filter: '',
      TableHead: [
        "Имя",
        "Фамилия",
        "Пол",
        "Никнейм",
        "Email",
        "Возраст",
        "Интересы",
        "Последняя активность в приложении",
        "Удален",
        "Заблокирован",
        ],
      users: [ ]
    }
  },
  methods: {
    BtnPlus() {
      this.Pagination = this.Pagination + 2
    },
    modalStart(u){
      this.modal = true;
      this.modalInput = u;
      console.log(this.modalInput)
    },
    filteredList() {
      if (this.checkboxDelite == true) {
        return this.users.filter(users => (users.neme.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.surname.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.nickname.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.email.toLowerCase().includes(this.filter.toLowerCase())) &&
        users.dell.includes(this.checkboxDelite) && 
        users.blocket.includes(this.checkboxBlocket))
      } else if (this.checkboxBlocket == true) {
        return this.users.filter(users => (users.neme.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.surname.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.nickname.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.email.toLowerCase().includes(this.filter.toLowerCase())) &&
        users.dell.includes(this.checkboxDelite) && 
        users.blocket.includes(this.checkboxBlocket))
      } else
        return this.users.filter(users => users.neme.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.surname.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.nickname.toLowerCase().includes(this.filter.toLowerCase()) ||
        users.email.toLowerCase().includes(this.filter.toLowerCase()))
      
    }
  },
  async mounted() {
    const usersI = await fetch('https://jsonplaceholder.typicode.com/users')
    const datausers = await usersI.json();
    console.log(datausers)
    for (let i = 0; i < datausers.length; i++) {
      if (i > 5) {
      const newRequest = {
        id: i,
        neme: datausers[i].name.split(" ")[0],
        surname: datausers[i].name.split(" ")[1],
        gender: 'N/A',
        nickname: datausers[i].username,
        email: datausers[i].email,
        old: 'N/A',
        interests: datausers[i].address,
        activity: new Date(),
        dell: 'false',
        blocket: 'true'
        
      };
    this.users.push(newRequest);
      } else {
        const newRequest = {
        id: i,  
        neme: datausers[i].name.split(" ")[0],
        surname: datausers[i].name.split(" ")[1],
        gender: 'N/A',
        nickname: datausers[i].username,
        email: datausers[i].email,
        old: 'N/A',
        interests: datausers[i].address,
        activity: new Date(),
        dell: 'true',
        blocket: 'false'
        
      };
    this.users.push(newRequest);
      }
    }
  }
}
</script>
